* {
  box-sizing: border-box;
}

html {
  color: #2f4f4f;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.catalog {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  justify-items: center;
  gap: 30px 40px;
  width: 100%;
  height: 100%;
  margin: 30px auto;
  display: grid;
}

@media (width >= 1000px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  transition-property: transform;
  transition-duration: 1s;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .person__image {
  border-radius: 25%;
}

.person:hover .person__extra {
  opacity: 1;
}

.person__image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition-property: border-radius;
  transition-duration: 1s;
}

.person__name {
  text-align: center;
  font-size: 20px;
  line-height: 23px;
}

.person__details {
  text-align: center;
}

.person__sport, .person__pay {
  color: #000;
  font-size: 18px;
  line-height: 20.7px;
}

.person__photo {
  position: relative;
}

.person__rank {
  color: #8b0000;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  line-height: 32px;
  display: block;
  position: absolute;
  bottom: 88px;
  right: 10px;
}

.person__extra {
  opacity: 0;
  transition-property: opacity;
}
